.about-content {
    width: 700px;
    margin: 0 auto;
    color: var(--color-primary-2);
    line-height: 2;
    font-size: 1rem;
}

@media (max-width:768px) {
    .about-content {
        max-width: 100%;
        padding: 0 1rem;
        text-align: center;
        margin-bottom: 2rem;
    }
}